*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
  }
  nav{
    background: #189AB4;
    height: 80px;
    width: 100%;
    overflow: hidden;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
  }
  label.logo{
    line-height: 80px;
    padding: 0 100px;
  }
  .logo-img{
    border-radius: 50%;
    position: relative;
    top:50%;
    transform: translateY(-50%);
  }
  nav ul{
    float: right;
    margin-right: 20px;
  }
  nav ul li{
    display: inline-block;
    line-height: 80px;
    margin: 0 5px;
  }
  .nav-links{
    color: #f7f7f7;
    font-size: 17px;
    padding: 7px 13px;
    border-radius: 3px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
  }
  .nav-links:hover{
    background-color:rgba(255, 255, 255, 0.2);
  }
  .checkbtn{
    font-size: 30px;
    color: #f7f7f7;
    float: right;
    line-height: 80px;
    margin-right: 40px;
    cursor: pointer;
    display: none;
  }
  #check{
    display: none;
  }
  @media (max-width: 952px){
    label.logo{
      font-size: 30px;
      padding-left: 50px;
    }
    nav ul li a{
      font-size: 16px;
    }
    .logo-img{
      width: 80px;
      position: relative;
      top:10px
    }
  }
  @media (max-width: 858px){
    .logo-img{
      display: none;
    }
    .checkbtn{ 
      display: block;
      width:100%;
      margin: 0;
      height: 80px;
    }
    .nav_menu{
      width: 9%;
    }
    ul{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: #2c3e50;
      top: 80px;
      left: -100%;
      text-align: center;
      transition: all .5s;
      z-index:500;
      margin-top: -23px;
    }
    nav ul li{
      display: block;
      margin: 50px 0;
      line-height: 30px;
    }
    nav ul li a{
      font-size: 20px;
    }
    a:hover,a.active{
      background: none;
      color: #0082e6;
    }
    #check:checked ~ ul{
      left: 0;
    }
    .button_div{
      display: flex;
      height: 80px;
      justify-content: space-between;
      align-items: center;
      padding: 20px;
    }
    .nav_menu--text{
      text-transform: uppercase;
      letter-spacing: 3px;
    }
  }
  @media screen and (max-width:550px) {
    
    nav{
      height: 60px;
    }
    .button_div{
      height: 60px;
    }
    .nav_menu--text{
     font-size: 1.7rem;
    }
  }
  @media screen and (max-width:450px) {
   .nav_menu{
     width:45px;
   }
   .nav_menu--text{
    font-size: 1.4rem;
   }
  }